import { Controller } from '@ali/4ever-cangjie';
import type { HoverCapture } from './utils/hoverCapture';
import type { IHoverToolbarPluginConfigs } from './types';

export default function createOnContextMenu(hoverCapture: HoverCapture, hoverToolbarConfig: IHoverToolbarPluginConfigs) {
  const { isDisabled } = hoverToolbarConfig;
  const disabled = isDisabled && isDisabled();
  return (event: React.MouseEvent, controller: Controller,next) => {
    if (!disabled) {
      hoverCapture.forceClose();
    }
    return next();
  }
}